@import url(https://fonts.googleapis.com/css?family=Poiret+One);
@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --rad: 0.7rem;
  --dur: 0.3s;
  --color-dark: #2f2f2f;
  --color-light: #fff;
  --color-brand: #57bd84;
  --font-fam: "Lato", sans-serif;
  --height: 5rem;
  --btn-width: 6rem;
  --bez: cubic-bezier(0, 0, 0.43, 1.49);
}

body {
  background-color: #f5f5f5;
  background: var(--color-dark);
  height: 100vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
}

.wrap {
  display: flex;
  justify-content: center;
  text-align: center;
  /* margin-top: -150px; */
  
}

.search {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
}

.search input {
  padding-left: 10px;
}

.search input:focus {
  
}

.searchButton {
  width: 80px;
  height: 32px;
  border: none;
  background: #048b9c;
  text-align: center;
  color: #fff;  
  cursor: pointer;
  outline: none;
  font-size: 14px;
  margin-left: -10px
}

.searchButton:hover {
  background-color: #17b8ce;
}
